import React from 'react'
function Alllink({name1,url1,inp1,date,count}) {
  if(inp1==null)inp1="";
  return (
    <div className='col col-sm-4 col-lg-3 col-md-3 col-xl-1' >
      <a href={url1} title={url1+" "+inp1+'<br>'+count} target='_blank'className='sd'><span className='sd1'> {name1} </span>
      </a></div>
     
      
     
  )
}

export default Alllink