// import {getFirestore,collection,doc,setDoc } from 'firebase/firestore'
import {db} from "./Firebaseconfig"
import { collection,addDoc ,setDoc, doc,get,query,limit, getDoc,orderBy, getDocs,onSnapshot,CACHE_SIZE_UNLIMITED } from "firebase/firestore";
