  const  gett =async (e)=>{
    e.preventDefault();
  try {  const q =  query(collection(db, "url321/"), orderBy("count", "desc"));
  const querySnapshot =await getDocs(q);
  querySnapshot.forEach((doc) => {
    // doc.data() is never undefined for query doc snapshots
    console.log(doc.id, " => ", doc.data());
    //  setUrlcol({id:doc.id,data:doc.data() })
        setUrlcol({id:doc.id ,data:doc.data()});
  });}
  catch (e) {
    console.error("Error adding document: ", e);
  }
}
/*      ***************************func call on button click for get data from firestore ********* end ***************        */ 
