import React, { Component } from 'react'
import {db} from "./Firebaseconfig"
import {set,ref} from "firebase/database"
export class Clafirestore extends Component {
constructor(props) {
  super(props)

  this.state = {
     name:'',url:''
      }
      this.firestoreed=this.firestoreed.bind(this);
}
firestoreed(e){
e.preventDefault();
const data={
    name:this.state.name,url:this.state.url  

}
const reee=ref(db,'fafafafa');
set(reee, {
  name:this.state.name,url:this.state.url  

});


}

  render() {
    return (
      <div>
         <form >
    <input value={this.state.name} placeholder='name' onChange={(e)=>this.setState(this.state.name= e.value)}></input>
    <input value={this.state.url} placeholder='url'onChange={(e)=>this.setState( this.state.url=e.value)} ></input>
    <button type='submit' onClick={this.firestoreed}>Add to classs  firestore</button>
    </form> 
      </div>
    )
  }
}

export default Clafirestore
